// set default due interval in milliseconds,
// days * hours * minutes * seconds * ms
const due_in_ms = (7 * 24 * 60 * 60 * 1000)
const default_dueDate = function(startDate) {
    const dueDate = startDate + due_in_ms
    console.log(dueDate)
    return dueDate
}

export default class Task {
    constructor({taskName, startDate, dueDate, priority}) {
        this.taskName = taskName
        this.startDate = new Date(startDate||Date.now())
        this.dueDate = new Date(dueDate||default_dueDate(this.startDate))
        this.priority = priority||5
    }
    db_format() {
        return {
            taskName: this.taskName,
            startDate: this.startDate,
            dueDate: this.dueDate,
            priority: this.priority
        }
    }
}