import React from 'react'
import TaskFormSimple from './TaskFormSimple'
import { useState } from 'react'

const Dashboard = ({xc}) => {
    console.log('render dash')
    const [showForm, setShowForm] = useState(false)
    const [container, setContainer] = useState()
    const display_task_Map = async function(map) {
        const results = await map()
        console.log(results)
        let formatted = ''
        results.forEach((value)=>{
            formatted = formatted.concat(JSON.stringify(value), '\n')
        })
        setContainer(formatted)
    }
    return (
        <>
            <p>{container}</p>
            <TaskFormSimple xc={xc} showForm={showForm} setShowForm={setShowForm}/>
            <nav>
                <button onClick={()=>display_task_Map(xc.get_all_tasks)}>get_all_tasks()</button>
                <button onClick={()=>display_task_Map(xc.get_started_tasks)}>get_started_tasks()</button>
                <button onClick={()=>setShowForm(!showForm)}>New Task</button>
            </nav>
        </>
    )
}

export default Dashboard;