import React from 'react'
import Task from './Task'
import { useState } from 'react'

const TaskFormSimple = ({xc, showForm, setShowForm}) => {
    console.log('render TaskFormSimple')
    function createTask(event) {
        event.preventDefault()
        const taskName = event.target.elements.TaskName_Input.value
        const startDate = event.target.elements.StartDate_Input.value
        const dueDate = event.target.elements.DueDate_Input.value
        const priority = event.target.elements.Priority_Input.value

        const newTask = new Task({taskName, startDate, dueDate, priority})
        xc.add_task(newTask.db_format())
        setShowForm(false)
    }
    const [priority, setPriority] = useState(5)
    const showPriority = function(event) {
        setPriority(event.target.value)
    }
    if (showForm) {
        return (
            <form onSubmit={createTask} id='taskFormSimple'>
                <input type="text" id="TaskName_Input" name="TaskName_Input" required={true} placeholder='Task Name'/><br/>
                
                <label htmlFor="StartDate_Input">Start Date:</label><br/>
                <input type="datetime-local" id="StartDate_Input" name="StartDate_Input"/><br/>
                
                <label htmlFor="DueDate_Input">Due Date:</label><br/>
                <input type="datetime-local" id="DueDate_Input" name="DueDate_Input"/><br/>
                
                <label htmlFor="Priority_Input">Priority: {priority}</label><br/>
                <input
                    type="range"
                    id="Priority_Input"
                    name="Priority_Input"
                    min="0"
                    max="10"
                    onChange={showPriority}
                    value={priority}
                />
                <br/><br/>
                <div className='Submit_or_Cancel_container'>
                    <input type="submit" value='Create' />
                    <button onClick={(event)=>{
                        event.preventDefault()
                        setShowForm(false)
                    }}>Cancel</button>
                </div>
            </form>
        )
    }
    else {return null}
}

export default TaskFormSimple;